import moment from 'moment'

export const getData = (mode, countryData, dayValueOfMovingAverage, startDate, endDate) => {
  let data = []
  const countryDataArray = Object.entries(countryData)

  if(mode === 'DAILY_CASE'){
    countryDataArray.map((dailyData, index) => {

      if(moment(dailyData[0]).isBetween(startDate, endDate)){
        let totalCases = 0
        let newCountryDataArray = []

        if(null != countryDataArray[index-dayValueOfMovingAverage]){
          newCountryDataArray = countryDataArray.slice(index-dayValueOfMovingAverage, index)
        }
        else{
          newCountryDataArray = countryDataArray.slice(0, index)
        }

        newCountryDataArray.map(newDailyData => {
          totalCases += newDailyData[1]["new_daily_cases"]
        })

        let movingAverage = totalCases / dayValueOfMovingAverage
        data.push({ date: moment(dailyData[0]).format("DD/M/YY"), average: Math.floor(movingAverage  * 100) / 100 , max: 10000 })
      }
    })
  }
  else if(mode === 'MORTALITY_RATE_PERCENTAGE'){
    countryDataArray.map((dailyData, index) => {

      if(moment(dailyData[0]).isBetween(startDate, endDate)){
        let totalPercentage = 0
        let newCountryDataArray = []

        if(null != countryDataArray[index-dayValueOfMovingAverage]){
          newCountryDataArray = countryDataArray.slice(index-dayValueOfMovingAverage, index)
        }
        else{
          newCountryDataArray = countryDataArray.slice(0, index)
        }

        newCountryDataArray.map(newDailyData => {
          totalPercentage += newDailyData[1]["total_deaths"] / newDailyData[1]["total_cases"]
        })

        let movingAverage = totalPercentage / dayValueOfMovingAverage
        data.push({ date: moment(dailyData[0]).format("DD/M/YY"), average: Math.floor(movingAverage  * 10000) / 10000 })
      }
    })
  }
  else if(mode === 'DAILY_DEATH_CHANGE'){
    countryDataArray.map((dailyData, index) => {

      if(moment(dailyData[0]).isBetween(startDate, endDate)){
        let dailydeathchange = 0
        // let newCountryDataArray = []

        if(null != countryDataArray[index-dayValueOfMovingAverage]){
          // newCountryDataArray = countryDataArray.slice(index-dayValueOfMovingAverage, index)
          dailydeathchange = countryDataArray[index][1]["new_daily_deaths"] - countryDataArray[index-dayValueOfMovingAverage][1]["new_daily_deaths"]
        }
        // else{
        //   newCountryDataArray = countryDataArray.slice(0, index)
        // }

        // newCountryDataArray.map(newDailyData => {
        //   totalPercentage += newDailyData[1]["total_deaths"] / newDailyData[1]["total_cases"]
        // })

        // let movingAverage = totalPercentage / dayValueOfMovingAverage
        data.push({ date: moment(dailyData[0]).format("DD/M/YY"), dailydeathchange: dailydeathchange })
      }
    })
  }


  return data
}

