import React, { Component } from 'react'
import moment from 'moment'
import { BarChart, Bar, Cell, CartesianGrid, XAxis, YAxis, Tooltip, ResponsiveContainer, ReferenceLine } from 'recharts'
import DatePicker from "react-datepicker"

import { getMovingAverageDayInterval } from '../utils/constants'
import { getData } from '../utils/covidCalculations'
import './BarCharts.css'

class BarCharts extends Component {
  constructor(props){
    super(props)
    this.state = {
      dayValueOfMovingAverage: getMovingAverageDayInterval(this.props.mode),
      startDate: moment().subtract(60, "days").format("M/DD/YY"),
      endDate: moment().format("M/DD/YY")
    }
  }

  render() {
    const { mode, countryData } = this.props
    if(null == countryData){
      return null
    }
    const { dayValueOfMovingAverage, startDate, endDate } = this.state
    let name
    switch (mode) {
        case 'DAILY_DEATH_CHANGE':
            name = 'Daily new deaths changed from the previous day’s death count'
            break
        default:
            name = ''
    }
    const data = getData(mode, countryData, dayValueOfMovingAverage, startDate, endDate)

    // const countryDataArray = Object.entries(countryData)
    // console.log("countryDataArray", countryDataArray)
    // countryDataArray.map((dailyData) => {
    //   console.log("moment", moment(dailyData[0]).isBetween(this.state.startDate, this.state.endDate))
    //   if(moment(dailyData[0]).isBetween(this.state.startDate, this.state.endDate)){
    //   // console.log("moment", moment(dailyData[0]))
    //   // console.log("moment type", typeof moment(dailyData[0]))
    //   console.log("dailyData",dailyData[1])
    //   data.push({x: moment(dailyData[0]).format("M/DD/YY"), y: dailyData[1]["new_daily_cases"]})
    //   }
    // })


    return (
      <>
      <div className="barchart-container">
      <div className="barchart-name">
        {name}
      </div>
      <div className="barchart-wrapper">
      <ResponsiveContainer width="95%" height={350} display="inline-block">
      <BarChart data={data} margin={{ top: 5, right: 15, left: 15, bottom: 5 }}>
        <Bar dataKey="dailydeathchange" fill="#8884d8" >
        {
           data.map((entry, index) => (
             <Cell key={`cell-${index}`} fill={entry.dailydeathchange > 0 ? "#D62728" : "#2E8B57"}/>
           ))
         }
        </Bar>
        <XAxis dataKey="date" interval="preserveEnd" >
        </XAxis>

        <YAxis type="number" domain={['dataMin', 'dataMax']} >
        </YAxis>
        <ReferenceLine y={0} stroke="#000" />
        <Tooltip />
      </BarChart>
      </ResponsiveContainer>
      </div>
      </div>
      <div className="barchart-dates-container">
      <div className="barchart-date-container barchart-startdate">
      <span>Start date</span>
      <DatePicker
        dateFormat="dd/M/yy"
        selected={new Date(startDate)}
      />
      </div>
      <div className="barchart-date-container barchart-enddate">
      <span>End date</span>
      <DatePicker
        dateFormat="dd/M/yy"
        selected={new Date(endDate)}
      />
      </div>
      </div>
      </>
    )
  }
}

export default BarCharts
