import React, { Component } from 'react'
import { connect } from 'react-redux'
import moment from 'moment'
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip} from 'recharts'

import LineCharts from '../../components/LineChart'
import BarCharts from '../../components/BarCharts'
import { setCountry } from "../../state/actions/countryActions"
import { COUNTRIES } from '../../utils/countryList'
import './Home.css'

const mapStateToProps = state => {
  return {
    country: state.country
  }
}

const actionCreators = {
  setCountry
}

class Home extends Component {
  constructor(props){
    super(props)
    this.state = {
    }
  }

  componentDidMount(){
    this.getCountryData()
  }

  componentDidUpdate(prevProps){
    const { country } = this.props
    if(country !== prevProps.country){
      console.log('Updated')
      this.getCountryData()
    }
  }

  getCountryData = async () => {
    //TODO Her zaman json dönmyor
    const { country } = this.props
    console.log("country",country)
    const res = await
    // fetch(`https://api.covid19tracking.narrativa.com/api/2020-03-22/country/Turkey`)
    fetch(`https://api.thevirustracker.com/free-api?countryTimeline=` + country.value)
    let resCountryDataText = await res.text()
    resCountryDataText = resCountryDataText.split('<br />')
    console.log("resCountryDataText",resCountryDataText)
    const resCountryDataJSON = JSON.parse(resCountryDataText)
    if(null != resCountryDataJSON.timelineitems && resCountryDataJSON.timelineitems[0].stat === "ok"){
      this.setState({
        countryData: resCountryDataJSON.timelineitems[0]
      })
    }
    // const resCountryData = await res.json()
    // console.log("resCountryData",resCountryData)
    // if(null != resCountryData.timelineitems && resCountryData.timelineitems[0].stat === "ok"){
    //   this.setState({
    //     countryData: resCountryData.timelineitems[0]
    //   })
    // }
  }

  handleChangeCountry = (e) => {
    let countrySelect = document.getElementById("country-select")
    let selectedCountry = countrySelect.options[countrySelect.selectedIndex]
    const currentCountry = { value: selectedCountry.value, label: selectedCountry.text }
    console.log("currentCountry",currentCountry)
    this.props.setCountry(currentCountry)
    // this.props.history.push('/country/' + selectedCountry.value)
  }

  render() {
    return (
      <div className="Home">
        <h1>COVID-19 Country Data Visualization</h1>
        <select id="country-select" defaultValue="" onChange={this.handleChangeCountry}>
          <option value="" hidden>Choose a country</option>
          {
            null != COUNTRIES && COUNTRIES.map((Country)=>{
              return <option key={Country.value} value={Country.value}>{Country.label}</option>
            })
          }
        </select>
        <LineCharts
          mode = 'DAILY_CASE'
          countryData = {this.state.countryData}
        />
        <LineCharts
          mode = 'MORTALITY_RATE_PERCENTAGE'
          countryData = {this.state.countryData}
        />
        <BarCharts
          mode = 'DAILY_DEATH_CHANGE'
          countryData = {this.state.countryData}
        />
      </div>
    )
  }
}

export default connect(mapStateToProps, actionCreators)(Home)
