import React, { Component } from 'react'
// import { VictoryLine, VictoryChart, VictoryAxis, VictoryTheme, VictoryVoronoiContainer, VictoryZoomContainer } from 'victory'
import moment from 'moment'
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, ResponsiveContainer, ReferenceLine, ReferenceDot } from 'recharts'
import DatePicker from "react-datepicker"

import { getMovingAverageDayInterval } from '../utils/constants'
import { getData } from '../utils/covidCalculations'
import './LineChart.css'

class LineCharts extends Component {
  constructor(props){
    super(props)
    this.state = {
      dayValueOfMovingAverage: getMovingAverageDayInterval(this.props.mode),
      startDate: moment().subtract(60, "days").format("M/DD/YY"),
      endDate: moment().format("M/DD/YY")
    }
  }

  render() {
    const { mode, countryData } = this.props
    if(null == countryData){
      return null
    }
    const { dayValueOfMovingAverage, startDate, endDate } = this.state
    let name
    switch (mode) {
        case 'DAILY_CASE':
            name = 'moving average of daily cases'
            break
        case 'MORTALITY_RATE_PERCENTAGE':
            name = 'moving average of mortality rate'
            break
        default:
            name = 7
    }
    const data = getData(mode, countryData, dayValueOfMovingAverage, startDate, endDate)
    let dataMaxX = ''
    let dataMaxY = 0
    data.map((entry, index) => {
      if(data[index].average > dataMaxY){
        dataMaxX = data[index].date
        dataMaxY = data[index].average
      }
    })
    console.log("dataMaxX",dataMaxX)
    console.log("dataMaxY",dataMaxY)
    // const countryDataArray = Object.entries(countryData)
    // console.log("countryDataArray", countryDataArray)
    // countryDataArray.map((dailyData) => {
    //   console.log("moment", moment(dailyData[0]).isBetween(this.state.startDate, this.state.endDate))
    //   if(moment(dailyData[0]).isBetween(this.state.startDate, this.state.endDate)){
    //   // console.log("moment", moment(dailyData[0]))
    //   // console.log("moment type", typeof moment(dailyData[0]))
    //   console.log("dailyData",dailyData[1])
    //   data.push({x: moment(dailyData[0]).format("M/DD/YY"), y: dailyData[1]["new_daily_cases"]})
    //   }
    // })


    return (
      <>
      <div className="linechart-container">
      {/*
      <VictoryChart
        theme={VictoryTheme.material}
        minDomain={{ y: 0 }}
        containerComponent={
          <VictoryVoronoiContainer
            mouseFollowTooltips
            labels={({ datum }) => `${datum.x}, ${(datum.y).toFixed(2)}`}
          />
        }
      >
        <VictoryAxis standalone={false} fixLabelOverlap={true} tickCount={4}/>
        <VictoryAxis dependentAxis standalone={false} />
        <VictoryLine
          data={data}
        />
      </VictoryChart>
*/}
      <div className="linechart-name">
        <select>
        <option value={dayValueOfMovingAverage}>{dayValueOfMovingAverage}-day</option>
        </select>
        {name}
      </div>
      <div className="linechart-wrapper">
      <ResponsiveContainer width="95%" height={350} display="inline-block">
      <LineChart data={data} margin={{ top: 5, right: 15, left: 15, bottom: 5 }}>
        <Line type="monotone" dataKey="average" stroke="#d88484" isAnimationActive={false} dot={false} />
        <CartesianGrid stroke="#ccc" strokeDasharray="3 3" />
        <XAxis dataKey="date" interval="preserveEnd" >
        </XAxis>

        <YAxis type="number" interval="preserveStartEnd" domain={[0, dataMax => dataMax * 1.2 < 10 ? Math.floor(dataMax * 1.2  * 100) / 100 : Math.ceil(Math.trunc(dataMax)/100)*100]} >
        </YAxis>
        <Tooltip />
        <ReferenceDot isFront={true} x={dataMaxX} y={dataMaxY} label="MAX" fill="red" stroke="none"/>
      </LineChart>
      </ResponsiveContainer>
      </div>
      </div>
      <div className="linechart-dates-container">
      <div className="linechart-date-container linechart-startdate">
      <span>Start date</span>
      <DatePicker
        dateFormat="dd/M/yy"
        selected={new Date(startDate)}
      />
      </div>
      <div className="linechart-date-container linechart-enddate">
      <span>End date</span>
      <DatePicker
        dateFormat="dd/M/yy"
        selected={new Date(endDate)}
      />
      </div>
      </div>
      </>
    )
  }
}

export default LineCharts
