import React, { Component } from 'react'
import { connect } from 'react-redux'

import { setCountry } from "../../state/actions/countryActions"
import LineCharts from '../../components/LineChart'
import './Country.css'

const mapStateToProps = state => {
  return {
    country: state.country
  }
}

const actionCreators = {
  setCountry
}

class Country extends Component {
  constructor(props){
     super(props)
     this.state = {}
  }

  componentDidMount(){
    const currentCountry = { value: this.props.match.params.countryvalue }
    console.log("currentCountry",currentCountry)
    this.props.setCountry(currentCountry)
    this.getCountryData()
  }

  getCountryData = async () => {
    //TODO Her zaman json dönmyor
    const { country } = this.props
    console.log("country",country)
    const res = await
    // fetch(`https://api.covid19tracking.narrativa.com/api/2020-03-22/country/Turkey`)
    fetch(`https://api.thevirustracker.com/free-api?countryTimeline=` + country.value)
    let resCountryDataText = await res.text()
    resCountryDataText = resCountryDataText.split('<br />')
    console.log("resCountryDataText",resCountryDataText)
    const resCountryDataJSON = JSON.parse(resCountryDataText)
    if(null != resCountryDataJSON.timelineitems && resCountryDataJSON.timelineitems[0].stat === "ok"){
      this.setState({
        countryData: resCountryDataJSON.timelineitems[0]
      })
    }
    // const resCountryData = await res.json()
    // console.log("resCountryData",resCountryData)
    // if(null != resCountryData.timelineitems && resCountryData.timelineitems[0].stat === "ok"){
    //   this.setState({
    //     countryData: resCountryData.timelineitems[0]
    //   })
    // }
  }

  render() {
    return (
      <div className="country-container">
        <span>{this.props.country.label}</span>
        {
          null != this.state.countryData &&
          <LineCharts
            countryData = {this.state.countryData}
          />
        }
      </div>
    )
  }
}

export default connect(mapStateToProps, actionCreators)(Country)
